import React from 'react';
import { BaseTextFieldProps, StandardTextFieldProps, TextField, TextFieldClasses, TextFieldClassKey, TextFieldProps } from '@mui/material';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

import Slider from '@mui/material/Slider';
import Web3 from 'web3';

interface CalculatorProps {
    initialDepositWei: string;
    style?: React.CSSProperties;
}

class Calculator extends React.Component<CalculatorProps> {

    state = {
        days: 1,
        initialDeposit: 1,
        newDeposit: 0,
        daylyIncome: 0,
        daylyIncomeNew: 0
    }
    baseState: any;

    manualEditMode: boolean = false;


    constructor(props: CalculatorProps) {
        super(props);
        
        this.state.initialDeposit = parseFloat(Web3.utils.fromWei(props.initialDepositWei));
        this.setState({
            daylyIncome: this.state.initialDeposit / 100 * 1.5
        });

        this.baseState = this.state;
        // properties

        //  scope hitch
        //this.onChangeAmount = this.onChangeAmount.bind(this);
    }

    componentDidMount() { 
        this.calc(this.state.initialDeposit);
    }

    componentWillUnmount() { 

    }

    componentWillReceiveProps(props: CalculatorProps) { 
        //console.log("componentWillReceiveProps", props);
        if(this.manualEditMode == false){
            this.setState({
                initialDeposit: parseFloat(Web3.utils.fromWei(props.initialDepositWei))
            });
        }

        this.setState({
            daylyIncome: this.state.initialDeposit / 100 * 1.5
        });

        this.forceUpdate();
      }


    onChange_Days = (event: any) => {
     this.calcOutput(event.target.value);
    }

    calcOutput = (value: number) => {
        this.setState({days: value});

        let newDeposit = this.state.initialDeposit;
        let daylyIncome = 0;
        for(let i = 0; i < this.state.days; i++){
            let dailyCompountPart = (newDeposit / 100 * 1.5);
            dailyCompountPart = dailyCompountPart * 1.05;

            newDeposit += dailyCompountPart;
            daylyIncome = dailyCompountPart;            
        }

        this.setState({
            newDeposit: newDeposit,
            daylyIncomeNew: daylyIncome
        });   
    }

    onChange_Initial = (event: any) => {
        console.log("onchange")
        this.manualEditMode = true;
        this.state.initialDeposit = parseFloat(event.target.value);
        this.calc(parseFloat(event.target.value));
        //this.calcOutput(parseFloat(event.target.value));
    }

    onFocus_Initial = () => {
        this.manualEditMode = true;
    }

    calc = (initial: number) => {
        this.setState({
            initialDeposit: initial, 
            daylyIncome: initial / 100 * 1.5
        });
    }

    render() {
        return (
        <Card className={"minHeight-small"} sx={{ minWidth: 275 }}>
            <CardContent>
                <Typography sx={{ fontSize: 16, marginBottom: 2 }} color="secondary" gutterBottom>
                  Reinvest Calculator
                </Typography>
                <Typography sx={{ fontSize: 12, my: 2 }} color="text.secondary" gutterBottom>
                    Calculate your daily passive income considering a reinvestment period
                </Typography>
                <Typography variant="h2">
                    <TextField label={"Initial Invest in BNB"} type="number" onFocus={this.onFocus_Initial} onChange={this.onChange_Initial} fullWidth={true}  defaultValue={1} ></TextField>
                </Typography>
                <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                    Daily 1.5%: {(this.state.daylyIncome).toFixed(3)} BNB
                </Typography>

                <Typography variant="h5" component="div" sx={{my: 2}}>
                    Reinvest for {this.state.days} Days
                </Typography>


                <Slider
                    defaultValue={1}
                    step={1}
                    min={1}
                    max={365}
                    onChange={this.onChange_Days}
                    sx={{my: 2}}
                /> 
                
                <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                    Deposit after {this.state.days} Days: {(this.state.newDeposit).toFixed(3)} BNB
                </Typography>
                <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                    Daily 1.5% after {this.state.days} Days: {(this.state.daylyIncomeNew).toFixed(3)} BNB
                </Typography>





            </CardContent>
        </Card>
        );
    }
}

export default Calculator;